
*{
  padding: 0;
  margin: 0;
}
header img {
  height: 80px;
  margin-left: 3px;
  margin-right: 10px;
}
.cont{
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
.aaa{
  padding: 10px 25px;
  border: 1px wheat solid;
  
}
body{
  height: 120vh;
  font-family: sans-serif;
  background-color: goldenrod;
  margin-top: 80px;
  padding: 30px;
}
header{
  background-color: white;
  position:fixed;
  top:0;left: 0;right: 0;height: 80px;
  padding: 5px;
  display: flex;
  align-items: center;
  box-shadow: 0 0 25px 0 black;

}

header * {
  display: inline;

}
header li{
  margin: 20px;
}
Link{
  text-decoration: none;
  color: black;
}

header li a{
  color:black;
  text-decoration: none;
}
